import React, { useEffect } from 'react';
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import anime from 'animejs/lib/anime.es.js'
import FloatingCard from './floating-card'
import pic1 from "../images/5.jpg"
import pic2 from "../images/4.jpg"
import pic3 from "../images/7.jpg"


const Banner = () => {
  const data = useStaticQuery(graphql`
    query {
      pic1: file(relativePath: { eq: "5.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      pic2: file(relativePath: { eq: "4.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      pic3: file(relativePath: { eq: "7.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  useEffect(() => {
    // let t1 = anime.timeline({
    //   easing: 'easeOutExpo',
    //   duration: 1500
    // })
  
    // t1.add({
    //   targets: '.banner',
    //   backgroundColor: 'rgb(255, 244, 223)',
    // })
    // .add({
    //   targets: '.banner .main-text',
    //   translateX: 250,
    // })
  });

  return (
    <div className="banner">
      <div className="container">
        <div className="row">
          <div className="side-image left">
          <FloatingCard img={pic2} width='208px' height='208px' />
          </div>
          <div className="main-text">Oumayma Lahmer</div>
          <div className="main-image">
            {/* <Img fluid={data.pic1.childImageSharp.fluid} /> */}
            <FloatingCard img={pic1} width='430px' height='430px' />
          </div>
          <div className="side-image right">
          <FloatingCard img={pic3} width='208px' height='208px' />
          </div>
        </div>
        <div className="scroll">
          <span>Scroll down</span>
        </div>
      </div>
      <div className="fixed-misc">Visual Artist and Panda</div>
    </div>
  )
}
export default Banner
